// --sb-breadcrumbs-link-color
// --sb-breadcrumbs-font-weight
// --sb-breadcrumbs-active-font-weight

.sb-breadcrumbs {
  // For backward compatibility purposes children that don't have the class `sb-breadcrumb` will be styled as links by default.
  // Breadcrumb component should be used instead for clickable and none-clickable children.
  > :not(.sb-breadcrumb) {
    color: var(--sb-breadcrumbs-link-color, var(--sbt-theme-color-60));
    font-family: var(--sbt-font-stack-primary);
    font-size: var(--sbt-font-size-20);
    font-weight: var(--sb-breadcrumbs-font-weight, var(--sbt-font-weight-30));
    line-height: var(--sbt-line-height-ui);
    text-decoration: none;
  }

  // Breadcrumb divider
  > * {
    &:not(:last-child)::after {
      content: '\f054';
      cursor: default;
      display: inline-block;
      font-family: 'Font Awesome 6 Pro';
      // Font awesome requires font-weight 900 for solid icons in custom css classes
      // https://github.com/FortAwesome/Font-Awesome/issues/11946
      font-weight: var(--sbt-font-weight-90);
      opacity: 0.5;
      padding: 0 4px;
      position: relative;
      text-decoration: none;
      top: 1px;
    }

    // Active breadcrumb styles
    &:last-child {
      font-weight: var(
        --sb-breadcrumbs-active-font-weight,
        var(--sbt-font-weight-50)
      );
    }
  }
}
