// --sb-breadcrumbs-color
// --sb-breadcrumbs-link-color
// --sb-breadcrumbs-font-weight

.sb-breadcrumb {
  color: var(--sb-breadcrumbs-color, var(--sbt-cp-gray-50));
  font-family: var(--sbt-font-stack-primary);
  font-size: var(--sbt-font-size-20);
  font-weight: var(--sb-breadcrumbs-font-weight, var(--sbt-font-weight-30));
  line-height: var(--sbt-line-height-ui);

  &.sb-breadcrumb__link {
    color: var(--sb-breadcrumbs-link-color, var(--sbt-theme-color-60));
  }
}
